// Call plugin.
import { callActions } from '../../../../interface/actions'
import { COMPLEX_OPERATION_MESSAGES, STATUS_CODES } from '../../../../constants'

// Other plugins.
import BasicError, { callCodes } from '../../../../../errors'

export default function createHandler(container) {
  const { context, logManager } = container

  /**
   * A "session failure" notification has been received, indicating the operation failed.
   *    Finish the operation.
   *
   * @method remoteFailure
   * @param {Object} call The primary call for the operation.
   * @param {Object} params Parameters describing the incoming call.
   * @param {string} [params.reasonText] Human-readable explanation for the call change.
   * @param {string} [params.statusCode] Code representing the reason for the call change.
   * @return {BasicError}
   */
  return function remoteFailure(call, params) {
    const reasonText = params.reasonText || COMPLEX_OPERATION_MESSAGES.CONSULTATIVE_TRANSFER_FAILURE
    const statusCode = params.statusCode || STATUS_CODES.CONSULTATIVE_TRANSFER_FAILURE

    const log = logManager.getLogger('CALL', call.id)
    log.info('Received call operation failed notice; handling.', { wrtcsSessionId: call.wrtcsSessionId })

    log.debug(`Processing as a local consultative transfer failure (${statusCode}).`)
    log.info('Finished handling call operation failed notice.')

    const error = new BasicError({
      message: `Consultative transfer operation failed: ${reasonText} (${statusCode})`,
      code: callCodes.GENERIC_ERROR
    })

    context.dispatch(
      callActions.consultativeTransferFinish(call.id, {
        error,
        transition: { reasonText, statusCode }
      })
    )

    // Since the function is specifically for errors, can return the error
    //    instead of needing to throw it. The flow handles it.
    return error
  }
}
