// Request plugin.
import { defaultOptions, parseOptions } from './configs'
import createRequestHelper from './requestModule'
import pluginInterface from './interface'

// Other plugins.
import { update as updateConfig } from '../config/interface/actions'

// Utils.
import mergeValues from '@rbbn/utilities/mergeValues'

/**
 * WebRTC JS SDK's Request plugin based on HTTP protocol.
 */
export default pluginFactory()

/**
 * Factory function for creating a Request Plugin for any requests sent to Ribbon's WebRTC Gateway.
 * @return {Function} The actual Plugin creator function.
 */
function pluginFactory() {
  return function request(bottle, options = {}) {
    options = mergeValues(defaultOptions, options)
    parseOptions(options)

    /*
     * Init function to setup the Request plugin.
     * @param {Object} container The bottle container.
     */
    function initPlugin(container) {
      const { context } = container

      // Send the provided options to the store.
      // This will be `state.config[name]`.
      context.dispatch(updateConfig(options, pluginInterface.name))
    }

    // Register the component factory functions to the bottle.
    bottle.factory('RequestAPI', pluginInterface.createAPI)

    bottle.factory('sendRequest', createRequestHelper)

    bottle.defer(initPlugin)

    return {
      capabilities: ['restAuthorization'],
      name: pluginInterface.name
    }
  }
}
