// Call plugin.
import { CALL_STATES } from '../../../constants'
import { getCallById } from '../../../interface/selectors'
import { getMediaState } from '../../../utils/state'

/**
 * Operation flow for a join operation.
 *
 * This operation has its own flow because of special-handling needed:
 *    - It acts on three local calls (two existing, one new).
 *    - The existing calls' "response" notification is of two different types,
 *        depending on success or failure.
 *    - The new, joined call's response notification is of two different types,
 *        depending on success or failure.
 */
export default function createFlow(container, operation, stages) {
  const { context, logManager } = container

  /*
   * Boolean map between a call ID and whether that call has received remote feedback or not.
   */
  const responsesReceived = operation.calls.reduce((acc, cur) => {
    acc[cur.callId] = false
    return acc
  }, {})

  /*
   * Boolean whether the flow has performed its "resolve" functionality or not yet.
   *    This is used to ensure multiple notifications can't trigger it multiple times
   *    in a race-condition.
   */
  let hasResolved = false

  /**
   * Helper function to know if every call has received feedback.
   *    When this is true, that indicates the operation can resolve successfully.
   * Join receives 3 notifications to indicate it succeeded (a respondCallUpdate for
   *     new, joined call, and two sessionComplete for the existing calls).
   * @method hasAllFeedback
   * @returns {boolean}
   */
  function hasAllFeedback() {
    // The flow has all the feedback needed to complete the operation if it has received
    //    a notification for every call.
    return operation.calls.every(call => responsesReceived[call.callId] === true)
  }

  /**
   * A join operation has been started locally.
   * @method localChanges
   */
  async function localChanges(calls, params) {
    operation.tracker.start()

    /*
     * Validate the operation before starting it. If the operation deems that it
     *    can't be done right now, avoid doing any state tracking, etc. for the
     *    operation.
     */
    const validationError = stages.validate(context.getState(), calls)
    if (validationError) {
      // Update the Call Report to indicate the operation ended with an error.
      onComplete(validationError)
    }

    try {
      // Perform the operation.
      const result = await stages.localOffer(calls, ...params)

      // Update operation tracking; performing signalling.
      operation.tracker.update()
      await stages.signalling(calls, result)
    } catch (err) {
      // Finish operation tracking; operation has failed.
      onComplete(err)
    }
  }

  /**
   * A "respond call update" notification has been received for the new, joined
   *    call, which indicates a successful operation. Process the answer to
   *    establish the joined call.
   * This is a simplified version of LocalNegotiation flow's receiveResponse stage.
   * @method joinAnswer
   */
  async function joinAnswer(call, params) {
    responsesReceived[call.id] = true

    const wrtcsSessionId = call.wrtcsSessionId
    const log = logManager.getLogger('CALL', call ? call.id : undefined)

    // Before processing the response, update status to indicate the operation is resuming.
    operation.tracker.resume()

    if (call.state === CALL_STATES.ENDED) {
      log.info('Join response for ended call. Ignoring.', { wrtcsSessionId })
      return
    }

    log.info(`Processing remote response for local ${operation.type} operation.`)
    const mediaState = getMediaState(call)
    log.debug(`Current call info; State: ${call.state}, MediaState: ${mediaState}.`)

    try {
      await stages.remoteAnswer(call, params)
    } catch (err) {
      onComplete(err)
    }

    if (hasAllFeedback()) {
      onComplete()
    }
  }

  /**
   * A "call end" notification has been received for the new, joined call,
   *    which indicates a failed operation. The other calls do not receive
   *    notifications in this scenario, so all must be updated.
   * @method joinEnd
   */
  async function joinEnd(call, params) {
    let error
    await operation.calls.map(async info => {
      const call = getCallById(context.getState(), info.callId)
      if (info.role === 'joined') {
        return stages.joinedFailure(call, params)
      } else {
        error = await stages.remoteFailure(call, params)
      }
    })

    onComplete(error)
  }

  /**
   * A "session complete" notification has been received for an existing call,
   *    which indicates operation success. This call needs to be cleaned-up.
   * Note: Both existing calls receive their own notification, so only update
   *    one at a time.
   * @method remoteSuccess
   */
  async function remoteSuccess(call, params) {
    responsesReceived[call.id] = true

    /*
     * TODO: Pre-processing logic.
     *    - KJS-2175 Queue Inputs: Is the operation ready for this notification?
     *    - Update call report/status: From "waiting" to "processing"?
     */
    await stages.remoteSuccess(call, params)

    if (hasAllFeedback()) {
      onComplete()
    }
  }

  /**
   * A "session fail" notification has been received for an existing call,
   *    which indicates a failed operation.
   * Note: Both existing calls receive their own notification, so only update
   *    one at a time.
   * @method remoteFailure
   */
  async function remoteFailure(call, params) {
    /*
     * TODO: Pre-processing logic.
     *    - KJS-2175 Queue Inputs: Is the operation ready for this notification?
     *    - Update call report/status: From "waiting" to "processing"?
     */
    const error = await stages.remoteFailure(call, params)

    onComplete(error)
  }

  const feedbackHandlers = {
    respondCallUpdate: joinAnswer,
    callEnd: joinEnd,
    sessionComplete: remoteSuccess,
    sessionFail: remoteFailure
  }

  /**
   * Operation method for receiving any/all responses from the remote side.
   * @method receiveResponse
   * @param {Object} call
   * @param {Object} params
   * @param {string} params.eventType
   */
  async function receiveResponse(call, params) {
    const eventType = params.eventType
    if (!eventType) {
      throw new Error(`No event type provided for ${operation.type} feedback!`)
    } else if (typeof feedbackHandlers[eventType] === 'undefined') {
      throw new Error(`No ${eventType} on the flow!`)
    }

    return feedbackHandlers[eventType](call, params)
  }

  /**
   * "Resolve" functionality for the operation.
   * @method onComplete
   * @param {Object} [error] If a failure scenario, the error information.
   * @throws {Error}
   */
  function onComplete(error) {
    if (hasResolved) {
      return
    }
    hasResolved = true

    operation.reportEvent.endEvent(error)
    operation.tracker.finish(error)

    if (error) {
      // Throw the error so the CallManager handles it as a failure.
      throw error
    }
  }

  return {
    start: localChanges,
    receiveResponse
  }
}
